.form-section {
  // display: flex;
  display: grid;
  grid-template-columns: repeat(2, max-content);
  min-height: 763px;
  padding: 255px 3% 3% 3%;
  // justify-content: center;
  // flex-wrap: wrap;
  column-gap: 160px;
  row-gap: 160px;
}

.form-section__wrapper h1 {
  font-size: 46px;
  line-height: 64px;
  color: $basic-white;
  letter-spacing: -0.005em;
  margin: 0;
  padding-top: 27px;
  font-weight: 700;
}

.form-section__wrapper {
  width: 393px;
  // margin-right: 80px;
}

// .form-section__item + .form-section__item {
//   margin-left: 160px;
//   margin-bottom: 160px;
// }

.form-section__wrapper p {
  margin: 0;
  padding-top: 24px;
  color: rgba($basic-white, 0.8);
  font-size: 20px;
  line-height: 32px;
  font-weight: 300;
  letter-spacing: -0.005em;
}

.form-section__form-wrapper {
  width: 326px;
  // padding-left: 80px;
  box-sizing: content-box;
}

.form-section__form-wrapper span {
  display: flex;
  color: $basic-white;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  letter-spacing: -0.005em;
  text-align: center;
  margin-top: 0;
  padding-top: 6px;
  margin-bottom: 18px;
}

@media (max-width: $tablet-width) {
  .main-page--no-js h1,
  .main-page--no-js p {
    color: $title-font;
  }

  .form-section {
    display: flex;
    min-height: auto;
    flex-direction: column;
    justify-content: start;
    padding-top: 85px;
    align-items: center;
    row-gap: 0;
    padding-bottom: 0;
  }

  .form-section .logo {
    display: none;
  }

  .form-section__wrapper {
    margin: 0;
    min-height: 205px;
    width: 398px;
  }

  .form-section__wrapper h1 {
    padding-top: 0;
    margin: 0;
    font-size: 30px;
    line-height: 42px;
    white-space: nowrap;
  }

  .form-section__wrapper p {
    padding-top: 19px;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
  }

  .form-section__form-wrapper span {
    color: $basic-black;
    font-size: 18px;
    line-height: 28px;
    margin-bottom: 30px;
  }

  .form-section__form-wrapper {
    padding-left: 0;
    min-height: 413px;
    padding-top: 64px;
    width: 321px;
    box-sizing: border-box;
  }
}

@media (max-width: $mobile-width) {
  .form-section {
    width: 100%;
    padding-top: 71px;
    padding-left: 16px;
    padding-right: 16px;
  }

  .form-section__wrapper h1 {
    margin: 0;
    white-space: normal;
    text-align: center;
    font-size: 30px;
    line-height: 42px;
  }

  .form-section__wrapper p {
    padding-top: 20px;
    font-size: 18px;
    line-height: 32px;
  }

  .form-section__form-wrapper,
  .form-section__wrapper {
    width: 100%;
  }

  .form-section__form-wrapper {
    padding-top: 43px;
    min-height: 410px;
    box-sizing: border-box;
  }

  .form-section__wrapper {
    min-height: 290px;
  }

  .form-section__form-wrapper span {
    margin-bottom: 20px;
  }
}
